const fs = require('fs');
const {
    readFile,
    readFileSync,
    writeFile,
    eachFile,
    eachDirectory,
    fileCopy
} = require('./file.js');

const dict = {
    "js": "javascript"
};

eachDirectory("./", function (pathname, dir1, name) {
    if (name.toLowerCase() === "md.source") {
        let codeDir = dir1;
        if (fs.existsSync(`${dir1}/code/link`)) {
            codeDir = readFileSync(`${dir1}/code/link`)
        }
        eachFile(pathname, function (pathname, dir, name, type) {
            if (type.toLowerCase() === "md") {
                readFile(pathname).then((content) => {
                    writeFile(`${dir1}/md/${name}`, content.replace(/```.+\..+\s/g, function (value) {
                        const filename = value.replace(/```/g, "").trim();
                        const type = filename.split(".").pop().toLowerCase();
                        fileCopy(`${codeDir}/${filename}`,`${dir1}/${filename}`)
                        const code = readFileSync(`${codeDir}/${filename}`);
                        return "```" + (dict[type] || type) + "\n" + code;
                    }));//+"\n\n[![996.icu](https://img.shields.io/badge/link-996.icu-red.svg)](https://996.icu)")
                })
            }
        })
    }
}, true);

