const fs = require('fs');

function readFile(pathname) {
    return new Promise((resolve, reject) => {
        fs.readFile(pathname, (error, data) => {
            if (error) {
                reject(error);
                return;
            }
            resolve(data.toString())
        })
    })
}

function writeFile(pathname, content) {
    return new Promise((resolve, reject) => {
        fs.writeFile(pathname, content, (error) => {
            if (error) {
                reject(error);
                return;
            }
            resolve(1)
        })
    })
}

function readFileSync(pathname) {
    return fs.readFileSync(pathname).toString()
}

function writeFileSync(pathname, content) {
    fs.writeFileSync(pathname, content)
}

function eachDirectory(dir, callback, recursive) {
    dir += "/";

    fs.readdir(dir, (file, files) => {
        if (files && files.length) {
            files.forEach((file, i) => {
                const pathname = dir + file;
                if (fs.statSync(pathname).isDirectory()) {
                    callback(pathname, dir, file);
                    if (recursive) {
                        eachDirectory(pathname, callback, recursive);
                    }
                }
            })
        }
    });
}

function eachFile(dir, callback, recursive) {
    dir += "/";

    fs.readdir(dir, (file, files) => {
        if (files && files.length) {
            files.forEach((file, i) => {
                const filename = dir + file;
                const ary = file.split(".");
                const type = ary.length > 1 ? ary.pop() : "";
                if (!fs.statSync(filename).isDirectory()) {
                    callback(filename, dir, file, type);
                    if (recursive) {
                        eachDirectory(filename, callback, recursive);
                    }
                }
            })
        }
    });
}

function fileCopy(src, dst) {
	fs.createReadStream(src).pipe(fs.createWriteStream(dst));
}

module.exports = {
    readFile,
    writeFile,
    readFileSync,
    writeFileSync,
    eachDirectory,
    eachFile,
    fileCopy
}


